import React from 'react';
import { hot } from 'react-hot-loader/root';
import classNames from 'classnames/bind';
import PropTypes from 'prop-types';
import { Calculator } from '@components/index.ts';

const style = require('./index.scss');
const cx = classNames.bind(style);

interface IOwnProps {
  type: string;
  title: string;
  onSetDisplayValue: (value: string) => void;
  onSetIsOpenLayer: (isOpen: boolean) => void;
  onSetTitle: (title: string) => void;
  onSave: () => void;
}

const ExpenseInput: React.FC<IOwnProps> = ({
  type,
  title,
  onSetDisplayValue,
  onSetIsOpenLayer,
  onSetTitle,
  onSave
}) => {
  const setDisplayValue = (displayValue: string) => {
    onSetDisplayValue(displayValue);
  };

  const handleClickSave = () => {
    onSave();
  };

  return (
    <div className={cx('expense_input', type.toLowerCase())}>
      <input
        type="text"
        className={cx('text_input')}
        placeholder='   ()'
        value={title}
        onChange={e => onSetTitle(e.currentTarget.value)}
      />
      <div className={cx('keyboard_area')}>
        <div className={cx('btn_area')}>
          <button className={cx('btn', 'cancle')} onClick={() => onSetIsOpenLayer(false)}></button>
          <button className={cx('btn', 'save')} onClick={handleClickSave}></button>
        </div>
        <Calculator
          onSetDisplayValue={setDisplayValue}
          type={type}
        />
      </div>
    </div>
  );
};

export default hot(ExpenseInput);
